function BasicTable() {
  const classes = useStyles();

  return (
    <TableContainer component={Paper}>
      <Table className={classes.table} aria-label="simple table">
        <TableHead>
          <TableRow>
            <TableCell>ID </TableCell>
            <TableCell >Umístění</TableCell>
          </TableRow>
        </TableHead>
        <TableBody>
          {items.map((row) => (
            <TableRow key={row.ID}>
              <TableCell  >{row.ID}</TableCell>
              <TableCell>
                <Autocomplete
                  multiple
                  id="combo-box-demo"
                  options={items.filter(item => item.category !== row.category)}
                  groupBy={(item) => item.category}
                  getOptionLabel={(option) => option.ID}
                  style={{ width: '300px', color: '#000000'}}
                  renderInput={(params) => <TextField {...params}  label="ID položky" color='primary' />}
                />
              </TableCell>
              
            </TableRow>
          ))}
        </TableBody>
      </Table>
    </TableContainer>
  );
}


